/**
 * @description 
 */
import {IStore} from "./interface"
class Store implements IStore{
    src: string
    overflow?: string
    loading?: string // iframe加载url时的显示内容
    frameBorder?: number
    position?: string
    id: string
    className?: string
    height: string // any number above 0, or 1% to 100%
    width: string // any number above 0, or 1% to 100%
    sandbox?: string //add optional sandbox values ("allow-forms allow-pointer-lock allow-popups allow-same-origin allow-scripts allow-top-navigation")
    allow?: string // add optional allow values ("geolocation microphone camera midi encrypted-media & more")
    styles?: object

    constructor(props: any = {}) {
        this.src = props.src 
        this.overflow = props.overflow
        this.loading = props.loading
        this.frameBorder = props.frameBorder
        this.position = props.position
        this.id = props.id
        this.className = props.className
        this.height = props.height || '250px'
        this.width = props.width || '520px'
        this.sandbox = props.sandbox
        this.allow = props.allow
        this.styles = props.styles

    }
}

export default Store;

