import React, {useContext} from "react";
import Store from './store';
import DashboardProcess from "./DashboardProcess";
import { observer } from "mobx-react";
import HyIframe from "@/controls/hy-iframe";
import {Connector} from "@/service/process";
import { TokenContext } from "@/pages/_internal/app/middlewares/AppAuthorized";
require('./style.scss');


export default observer((props) => {
    const tokenContext = useContext(TokenContext);
    const store = new Store({HyIframe,connect:Connector,tokenContext, ...props});
    return (
        <DashboardProcess {...store} />
    )
});
