import { observable, action } from "mobx";
import BaseStore from "@/pages/store";
import { AxiosInstance } from "axios";
import { getUnreadNumbers } from "@/service/http";
class Store extends BaseStore {
  constructor(props: any) {
    super(props);
    this.DataApiService = props.DataApiService;
    this.tokenContext = props.tokenContext;
  }
  @observable home: any = {};
  @observable messages: number = 0;
  @observable process: any = {};

  DataApiService: AxiosInstance;
  tokenContext: IToken;

  getHomeData() {}
  @action
  getMessagesData() {
    const userId = this.tokenContext.userId;
    return getUnreadNumbers(userId)
      .then((res: any) => {
        this.messages = res.result.iTotalRecords;
      })
      .catch((err: string) => {});
  }

  getProcessData() {}

  getMyData() {}
}

export default Store;
