import React, { useContext, useEffect, useState } from "react";
import { TokenContext } from "@/pages/_internal/app/middlewares/AppAuthorized";
import { observer, inject } from "mobx-react";
import ListView from "@/components/ListView/listview";
import MessageItem from "./MessageItem";
import EasyNarBar from "@/components/EasyNarBar";

require("./style.scss");

const Messages = observer((props: any) => {
  const { selected } = props;
  const { changeReadStatu } = props.store;
  const tokenContext = useContext(TokenContext);
  const [height, setHeight] = useState(1);
  useEffect(() => {
    const NarBar = document.querySelector(".am-navbar");
    let Height = Math.max(document.documentElement.clientHeight, document.body.clientHeight);
    Height = Height - (NarBar ? NarBar.offsetHeight : 0);
    setHeight(Height);
  }, []);
  useEffect(() => {
    if (selected) {
      pageIndexrest();
      getAllmessage();
    }
  }, [selected]);
  const renderRow = (rowData: any) => {
    return <MessageItem MessData={rowData} userName={tokenContext.userName} changeReadStatu={changeReadStatu}></MessageItem>;
  };
  const separator = (sectionID: string, rowID: string) => (
    <div
      key={`${sectionID}-${rowID}`}
      style={{
        backgroundColor: "#F5F5F9",
        width: "100%",
        height: "0",
        borderBottom: "1px solid #ECECED"
      }}
    />
  );
  const { message, onEndReached, onRefresh, hasMore, isLoading, getAllmessage, pageIndexrest } = props.store;
  return (
    <div className="Messagesmaindiv">
      <EasyNarBar mainContent="消息通知" />
      <div className="MessagesListDiv">
        <ListView
          dataSource={message}
          renderRow={renderRow}
          pageSize={20}
          onEndReached={onEndReached}
          onRefresh={onRefresh}
          height={height}
          hasMore={hasMore}
          isLoading={isLoading}
        ></ListView>
      </div>
    </div>
  );
});

export default inject((stores: any) => ({ store: stores.messagesStore }))(Messages);
