class Store{
    constructor(props) {
        this.HyIframe = props.HyIframe;
        this.selected = props.selected;
        this.connect = props.connect;
        this.tokenContext = props.tokenContext;
    }
    HyIframe: TReactNode;
    selected: void| boolean;
    connect: Function;
    tokenContext: IToken;
}

export default Store;