import React, { useEffect } from "react";
import { observer, inject } from "mobx-react";
import { Checkbox, List } from "antd-mobile";

import { UserOutlined, IdcardOutlined, UnlockOutlined, LogoutOutlined } from "@ant-design/icons";
require("./style.scss");

import EasyNarBar from "@/components/EasyNarBar";

import { useHistory } from "react-router-dom";

const Personal = observer((props: any) => {
  const history = useHistory();
  useEffect(() => {
    props.store.getUserInfo();
  }, []);
  const outLogin = () => {
    const callback = (url: string) => {
      window.location.replace(url);
    };
    props.store.QuitSys(callback);
  };
  const { orgName, name } = props.store;
  return (
    <>
      <EasyNarBar mainContent="个人中心" />
      <List>
        <List.Item extra={name} thumb={<UserOutlined />} wrap={true}>
          姓名
        </List.Item>
        <List.Item extra={orgName} thumb={<IdcardOutlined />}>
          机构
        </List.Item>
        <List.Item
          thumb={<UnlockOutlined />}
          arrow="horizontal"
          onClick={() => {
            history.push({ pathname: "/password" });
          }}
        >
          修改密码
        </List.Item>
        <List.Item thumb={<LogoutOutlined />} arrow="horizontal" onClick={outLogin}>
          退出登录
        </List.Item>
      </List>
    </>
  );
});

export default inject((stores: any) => ({
  store: stores.personalStore
}))(Personal);
