import HomeBarIcon from "@/assets/imgs/HomeBarIcon.png";
import HomeBarIconSelected from "@/assets/imgs/HomeBarIconSelected.png";
import MessageIcon from "@/assets/imgs/MessageIcon.png";
import MessageIconSelected from '@/assets/imgs/MessageIconSelected.png';
import ProcessIcon from '@/assets/imgs/home-process-icon.png';
import ProcessIconSelected from '@/assets/imgs/home-process-icon-selected.png';
import MyIcon from '@/assets/imgs/MyIcon.png';
import MyIconSelected from "@/assets/imgs/MyIconSelected.png";

const _getIconUrl = (url:string) => {
    return () => {
        return url;
    };
}

export default {
    getHomeIconUrl: _getIconUrl(HomeBarIcon),
    getHomeIconSelectedUrl: _getIconUrl(HomeBarIconSelected),
    getMessageIcon: _getIconUrl(MessageIcon),
    getMessageIconSelected: _getIconUrl(MessageIconSelected),
    getProcessIcon: _getIconUrl(ProcessIcon),
    getProcessIconSelected: _getIconUrl(ProcessIconSelected),
    getMyIcon: _getIconUrl(MyIcon),
    getMyIconSelected: _getIconUrl(MyIconSelected),
}
