import React, { useContext } from "react";
import Store from "./store";
import DashboardTabsBar from "./DashboardTabsBar";
import { observer } from "mobx-react";
import getHistoryContext from "@/utils/tools/getHistoryContext";
import { HistoryContext } from "@/router";
import DataApiService from "@/service/http";
import { TokenContext } from "@/pages/_internal/app/middlewares/AppAuthorized";
require("./style.scss");

export default observer((props: any) => {
  const { unreadMessageCount } = props;
  const history = getHistoryContext(useContext, HistoryContext);
  const tokenContext = useContext(TokenContext);
  const store = new Store({ DataApiService, tokenContext, ...props });

  return <DashboardTabsBar history={history} store={store} unreadMessageCount={unreadMessageCount} />;
});
