import React, { useRef, useEffect, useMemo, useState } from "react";
import { observer } from "mobx-react";
import storage from "@/utils/storage";
import { getHost, getClintHeight } from "@/utils/common";

export default observer(function (props: any) {
  // const src = "/FlowHYS/MobileShell.html?projectId=03a3dc03b1654233b828e787dcefffe8&pagename=STATIC_FLOW_MOBILE&username=%E5%8D%8E%E6%99%A8%E5%AE%87&userId=31cd2686212f4a84b34cc3ee79b4e2f5&ClientType=2&";
  //const userName = props.tokenContext.userName;
  const tokenId = storage.getItem("userToken");
  const userId = storage.getItem("userId");
  const client_token = storage.getItem("client_token");
  const [routeURL, setRouteURL] = useState("");
  const frame = useRef<HTMLIFrameElement>();
  const selected = props.selected;
  const onLoadiFrame = () => {
    const iframeWindow = frame.current.contentWindow;
    if (!navigator.userAgent.match(/iPad|iPhone/i)) {
      return false;
    }
    const iframebody = iframeWindow.document.body;
    iframebody.style.width = document.body.clientWidth + "px";
  };
  useEffect(() => {
    setRouteURL(`${window.baseURL || ""}/FlowMobile/home?clintType=wx&tokenId=${tokenId}&client_token=${client_token}&userId=${userId}`);
  }, []);
  return useMemo(
    () => (
      <>
        {selected && (
          <div style={{ overflow: "scroll", WebkitOverflowScrolling: "touch", width: "100%", height: `${getClintHeight()}px` }}>
            <iframe
              ref={frame}
              src={routeURL}
              frameBorder={0}
              style={{ width: "100%", display: "flex", height: `${getClintHeight()}px`, overflow: "visible" }}
              scrolling="no"
              onLoad={onLoadiFrame}
            />
          </div>
        )}
      </>
    ),
    [selected, routeURL]
  );
});
