import React from 'react';
import { getDashboardTabHome } from './DashboardTabHome';
import DashboardCards from '@/components/DashboardCards';
import DashboardCarousel from '@/components/DashboardCarousel';
require('./style.scss');

const OuterComponents = () => {
    return (
        <div className="home-page">
            <DashboardCarousel />
            <DashboardCards />
        </div>
    );
};

const HomeContentWrapper = getDashboardTabHome(OuterComponents);

export default class DashboardTabHome extends React.Component {
    props: any;
    constructor(props) {
        super(props);
    }

    render() {
        return <HomeContentWrapper />;
    }
}
