import React from 'react';
/**
 * @description 封装了iframe第三方组件的项目核心组件， 提供与第三方定制页面通信的接口功能，在此基础上，可以在外层的展示型组件中调用此组件，并通过增加业务逻辑扩展功能，以满足项目的业务需求。
 */


export default React.forwardRef((props, ref) => {
    const { store } = props;
    const { src, frameBorder, width, height } = store;
    return (
        <iframe ref={ref} src={src} frameBorder={frameBorder} width={width} height={height} />
    )
});