import React from 'react';
import {getDashboardTabMessage} from "./DashboardTabMessage";
import DashboardMessage from "@/components/DashboardMessage";

const OuterComponents = (props) => {
    return (
        <div>
            <DashboardMessage {...props}  /> 
        </div>
    )
}

const HomeContentWrapper = getDashboardTabMessage(OuterComponents);

export default class DashboardTabMessage extends React.Component {
    props: any;
    constructor(props){
        super(props);
        this.props=props;
    }

    render() {
        const {selected} =this.props;
        return (
            <div>
                <HomeContentWrapper selected={selected} />
            </div>
        )
    }
}
