import { observable } from "mobx";
import { ISlide } from "./interface";
import BaseStore from "@/pages/store";
import image001 from "@/assets/imgs/1.png";
import image002 from "@/assets/imgs/2.png";
import image003 from "@/assets/imgs/3.png";

class Store extends BaseStore {
  constructor(props) {
    super(props);
  }
  @observable slides: ISlide[] = [
    {
      name: "image001",
      url: image001
    },
    {
      name: "image002",
      url: image002
    },
    {
      name: "image003",
      url: image003
    }
  ];
}

export default Store;
