import React from "react";
import { observer } from "mobx-react";
import { Carousel, WhiteSpace } from "antd-mobile";
import { ISlide } from "./interface";
const renderSlideItem = (imageData: ISlide) => {
  return (
    <img
      className="TopImg"
      src={imageData.url}
      alt={imageData.name}
      key={imageData.url}
      style={{ width: "100%", verticalAlign: "top" }}
      onLoad={() => {
        // fire window resize event to change height
        window.dispatchEvent(new Event("resize"));
      }}
    />
  );
};

@observer
class DashboardCarousel extends React.Component<any> {
  props: any;

  constructor(props) {
    super(props);
  }

  render() {
    const { store } = this.props;
    const { slides } = store;
    return (
      <Carousel infinite>
        {(slides &&
          slides.map((item: any) => {
            return renderSlideItem(item);
          })) ||
          null}
      </Carousel>
    );
  }
}

export default DashboardCarousel;
