require("./style.scss");
import React, { useEffect, useState, useMemo } from "react";

import DashboardCardItem from "./DashboardCardItem";
import { Grid } from "antd-mobile";

import { getMenulist } from "@/service/http";
import dataMapToIcon from "./icons";
import { ConsoleSqlOutlined } from "@ant-design/icons";

export default (props: any) => {
  const [list, setList] = useState(null);

  useEffect(() => {
    getMenulist({
      pid: "1e33ef67dd0c4f58826c9382ea9552dc"
    }).then((res: any) => {
      const { result } = res;
      if (res.code === 0) {
        const urls: any[] = [];
        const data: any[] = [];
        const res: any[] = result.filter((item: any) => item.addrType === 3);

        res.forEach(item => {
          if (urls.indexOf(item.remoteUrl) === -1) {
            urls.push(item.remoteUrl);
            data.push(item);
          }
        });
        // data.push({
        //     remoteUrl: '/meeting-notification/1',
        //     menuName: '测试签到'
        // })
        setList(data);
      }
    });
  }, []);
  return useMemo(
    () => (
      <Grid
        data={list}
        columnNum={4}
        square={false}
        hasLine={false}
        renderItem={(dataItem: any) => {
          const url = dataItem.remoteUrl;
          let icon = dataMapToIcon[url];
          if (!icon) {
            if (url.indexOf("请假审批") !== -1) {
              icon = dataMapToIcon["/flow-shell/1"];
            } else if (url.indexOf("非正常打卡") !== -1) {
              icon = dataMapToIcon["/flow-shell/2"];
            } else if (url.indexOf("销假审批") !== -1) {
              icon = dataMapToIcon["/flow-shell/3"];
            }
          }
          return <DashboardCardItem icon={icon} name={dataItem.menuName} url={url} />;
        }}
      ></Grid>
    ),
    [list]
  );
};
