import React from "react";
import { observer } from "mobx-react";
import { TabBar } from "antd-mobile";
import DashboardTabHome from "./DashboardTabHome";
import BarIconUrlService from "./service/BarIconUrlService";
import DashboardTabProcess from "./DashboardTabProcess";
import DashboardTabMessage from "./DashboardTabMessage";
import DashboardTabMy from "./DashboardTabMy";
import getSearchParams from "@/utils/tools/getSearchParams";
import { toJS } from "mobx";

const subscribe = function (this: any, subscribeType: string, fn: Function) {
  window.addEventListener("message", e => {
    const { type } = e.data;
    if (type === subscribeType) {
      fn && fn(this);
    }
  });
};

@observer
class DashboardTabsBar extends React.Component<any> {
  props: any;
  state: any;
  constructor(props: any) {
    super(props);
    this.state = {
      homeSelected: false,
      processSelected: false,
      messageSelected: false,
      mySelected: false
    };
    this.pressHandler = this.pressHandler.bind(this);
  }

  pressHandler(this: any, key: any) {
    key = key || "homeSelected";
    const resetMap = {
      homeSelected: false,
      processSelected: false,
      messageSelected: false,
      mySelected: false
    };
    const stateMap = Object.assign(resetMap, { [key]: true });
    const history = this.props.history;
    history.push({ pathname: "/dashboard", search: `selected=${key}` });
    this.setState(stateMap);
  }

  componentDidMount() {
    const { store } = this.props;
    store.getMessagesData();
  }

  render() {
    const { store } = this.props;
    store.getMessagesData();
    // const { messages } = store;
    const { unreadMessageCount } = this.props;
    // let unreadMessageCountProps = 0;
    // if (unreadMessageCount == null) {
    //   unreadMessageCountProps = unreadMessageCount;
    // }
    // const totalUnreadMessage = messages + Number(unreadMessageCountProps);
    const searchMap = getSearchParams(window.location.search);
    const { selected = "homeSelected" } = searchMap;
    const state = Object.assign(
      {
        homeSelected: false,
        processSelected: false,
        messageSelected: false,
        mySelected: false
      },
      this.state,
      { [selected]: true }
    );

    const { homeSelected, processSelected, messageSelected, mySelected } = state;

    subscribe.call({}, "navbar-leftclick", () => {
      this.pressHandler("homeSelected");
    });

    return (
      <div
        style={{
          background: "#ffffff",
          position: "fixed",
          height: "100%",
          width: "100%",
          top: 0
        }}
      >
        <TabBar unselectedTintColor="#949494" tintColor="#33A3F4" barTintColor="white">
          <TabBar.Item
            title="首页"
            key="Dashboard"
            icon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getHomeIconUrl()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getHomeIconSelectedUrl()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selected={homeSelected} //除了交互选中还可以根据参数选择
            onPress={() => {
              this.pressHandler("homeSelected");
            }}
            data-seed="logId"
          >
            <DashboardTabHome selected={homeSelected} />
          </TabBar.Item>

          {/*  消息通知  */}

          <TabBar.Item
            title="消息"
            key="Message"
            icon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getMessageIcon()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getMessageIconSelected()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selected={messageSelected} //除了交互选中还可以根据参数选择
            badge={unreadMessageCount} // 跟消息推送相关
            onPress={() => {
              this.pressHandler("messageSelected");
            }}
            data-seed="logId"
          >
            <DashboardTabMessage selected={messageSelected} />
          </TabBar.Item>

          {/* 流程  */}

          <TabBar.Item
            title="流程"
            key="Process"
            icon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getProcessIcon()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getProcessIconSelected()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selected={processSelected} //除了交互选中还可以根据参数选择
            onPress={() => {
              this.pressHandler("processSelected");
            }}
            data-seed="logId"
          >
            <DashboardTabProcess selected={processSelected} />
          </TabBar.Item>

          {/** 我的 */}

          <TabBar.Item
            title="我的"
            key="My"
            icon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getMyIcon()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: "22px",
                  height: "22px",
                  background: `url(${BarIconUrlService.getMyIconSelected()}) center center /  21px 21px no-repeat`
                }}
              />
            }
            selected={mySelected} //除了交互选中还可以根据参数选择
            onPress={() => {
              this.pressHandler("mySelected");
            }}
            data-seed="logId"
          >
            <DashboardTabMy selected={mySelected} />
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default DashboardTabsBar;
