/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useMemo, useEffect, useState } from "react";
import { Icon } from "antd-mobile";
import { useHistory } from "react-router-dom";
import storage from "@/utils/storage";
import { changeReadStatue } from "@/service/http";
import { getHost } from "@/utils/common";
import classnames from "classnames";
const typeMaps = {
  "1001": "公告通知",
  "1002": "流程待办",
  "1000": "会议通知",
  "1003": "考勤",
  "1004": "消息",
  "1005": "履职待办"
};

const MessageItems = (props: any) => {
  const history = useHistory();
  const { MessData, userName, changeReadStatu } = props;
  const {
    wx_message_view_id,
    wx_message_view_title,
    wx_message_view_time,
    wx_message_view_type,
    wx_message_view_url,
    wx_message_view_source_id,
    wx_message_view_read_status,
    wx_message_view_origin_message
  } = MessData;
  const flowcontent = wx_message_view_type == "1002" ? JSON.parse(wx_message_view_origin_message).MessageContent : "";
  const linkToDetail = () => {
    if (wx_message_view_read_status == 0) {
      changeReadStatue({ id: wx_message_view_id }).then(res => {
        changeReadStatu(wx_message_view_id);
      });
    }
    switch (wx_message_view_type) {
      case "1000":
        history.push({
          pathname: `/meeting-notification/${wx_message_view_source_id}`
        });
        break;
      case "1002":
        const tokenId = storage.getItem("userToken");
        const userId = storage.getItem("userId");
        const client_token = storage.getItem("client_token");
        const routerUrl = `${
          window.baseURL || ""
        }/FlowMobile/home?clintType=wx&type=todo&userId=${userId}&client_token=${client_token}&tokenId=${tokenId}&flowdata=${encodeURIComponent(
          wx_message_view_url.split("=")[1]
        )}`;
        history.push({
          pathname: "/process",
          state: {
            routerUrl: routerUrl
          }
        });
        break;
      case "1005":
        history.push({
          pathname: "/perform_detail",
          state: {
            id: JSON.parse(wx_message_view_origin_message).urlCode,
            status: "0",
            TabIndex: "0"
          }
        });
        break;
      default:
        history.push({ pathname: "/dash_notice", state: { MessData: MessData } });
        break;
    }
  };
  useEffect(() => {}, [wx_message_view_read_status]);
  return useMemo(
    () => (
      <>
        <div className="MessageItemitemDiv" onClick={linkToDetail}>
          <div className={classnames("isread", { HadRead: wx_message_view_read_status == 1 })}> </div>
          <div className={classnames("MessageItemitem", { isFlow: wx_message_view_type == "1002" })}>
            <p className="messageViewTitle">{wx_message_view_title}</p>
            <p className="flowContent">{flowcontent}</p>
            <div className="buttonDiv">
              <p className={classnames("typeSpan", { isFlowTypeSpan: wx_message_view_type == "1002" })}>
                <span>{wx_message_view_time}</span>
                <span className={classnames("wxMessageViewType", { TwoWords: typeMaps[wx_message_view_type].length < 4 })}>
                  {typeMaps[wx_message_view_type]}
                  <Icon type="right" size="md" className="rightIcon"></Icon>
                </span>
              </p>
            </div>
          </div>
        </div>
      </>
    ),
    [wx_message_view_id, wx_message_view_title, wx_message_view_time]
  );
};

export default MessageItems;
