import React, { useContext, useEffect, useState } from "react";
import { TokenContext } from "@/pages/_internal/app/middlewares/AppAuthorized";
import { getUserInfo, getUnreadMessageCount } from "@/service/http";
import DashboardTabsBar from "@/components/DashboardTabsBar";
import storage from "@/utils/storage";
import socket from "@/pages/_internal/app/middlewares/AppAuthorized/websocket";
require("./style.scss");

const Dashboard = (props: any) => {
  const tokenContext = useContext(TokenContext);
  const [unreadMessageCount, setUnreadMessageCount] = useState(0);

  const getUserName = (): Promise<any> => {
    return new Promise((resolve, reject) => {
      getUserInfo()
        .then((res: any) => {
          if (res && res.code === 0) {
            const result = res.result;
            const baseUserInfoResult = JSON.parse(result.baseUserInfo) || {};
            const userName = baseUserInfoResult.loginname;
            resolve(userName);
          } else {
            reject(res.message);
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  };

  const fetchUnreadMessageCount = () => {
    getUnreadMessageCount({ userId: storage.getItem("userId"), readStatus: 0 }).then((res: any) => {
      //setUnreadMessageCount(res.result.iTotalRecords);
      socket.setUnreadMessageCount(res.result.iTotalRecords);
    });
  };

  const unreadMessageCountHandle = (count: number) => {
    console.log("---------unreadMessageCount----------");
    setUnreadMessageCount(count);
  };

  // 获取未读消息
  useEffect(() => {
    fetchUnreadMessageCount();
    emitter.on("updateMessageCount", unreadMessageCountHandle);
    //connect();
    // 获取用户Name
    getUserName().then((data: string) => {
      if (data) {
        storage.setItem("userName", data);
        tokenContext.updateItemInToken("userName", data);
      }
    });
    return () => {
      emitter.off("updateMessageCount", unreadMessageCountHandle);
    };
  }, []);

  return <DashboardTabsBar unreadMessageCount={unreadMessageCount} />;
};
export default React.memo(Dashboard);
